.form {
  margin: 0;
  margin-left: 20px;
  padding: 10px;
  width: 270px;
  height: 140px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  border: 1px solid black;
  position: relative;
}

.input {
  position: absolute;
  top: 35px;
  left: 10px;
  color: black;
  transition: 300ms;
  outline: none;
}

.input_number {
  position: absolute;
  transition: 300ms;
  outline: none;
  color: black;
  top: 95px;
  left: 10px;
}

.button {
  background-color: rgb(255, 255, 255);
  border: 1px solid rgba(0, 0, 0, 0.5);
  padding-right: 10px;
  padding-left: 10px;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 4px -2px black;
  transition: linear 200ms;
}
.button:active {
  box-shadow: 0 0 2px -2px black;
}
